import React, { useEffect, useState, useRef } from "react";
import axios from "axios";

export const List = ({ ID }) => {
  const [songs, setSongs] = useState([]);
  const [page, setPage] = useState(1);
  const [search, setSearch] = useState("");

  const options = {
    method: "GET",
    url: `https://genius.p.rapidapi.com/artists/${ID}/songs`,
    params: { page: page },
    headers: {
      "x-rapidapi-key": "9da9f853dcmsh86bba916aa656c2p18fc8djsne2562db22529",
      "x-rapidapi-host": "genius.p.rapidapi.com",
    },
  };

  useEffect(() => {
    axios
      .request(options)
      .then(function (response) {
        console.log(response.data.response);
        setSongs(response.data.response.songs);
      })
      .catch(function (error) {
        console.error(error);
      });
  }, [ID, page]);

  const nextPage = () => {
    setPage(page + 1);
  };

  const PreviousPage = () => {
    setPage(page - 1);
  };

  const handleChange = (e) => {
    setSearch(e.target.value);
  };
  const handleSort = (e) => {
    alert("Still worling on it");
  };

  const filteredQuery = songs.filter((song) =>
    song.title.toLowerCase().includes(search.toLocaleLowerCase())
  );

  return (
    <>
      <div className="container">
        <div className="row">
          <div
            className="col-sm-3 text-center"
            style={{ borderBottom: "1px solid white", opacity: "0.6" }}
          >
            <input placeholder="Search song" onChange={handleChange} />
          </div>

          <div
            className="col-sm-6 text-center"
            style={{ borderBottom: "1px solid white" }}
          >
            <button onClick={handleSort}>order A-Z</button>
            <button onClick={PreviousPage}>Previous</button>
            <button onClick={nextPage}>Next</button>
          </div>
        </div>
      </div>
      {ID ? (
        <div className="container-fluid">
          <div className="row">
            {filteredQuery.map((song) => (
              <>
                <div className="col-sm-3">
                  <img
                    alt="..."
                    width="250"
                    class="mb-2 img-thumbnail "
                    src={song ? song?.song_art_image_url : null}
                  />
                  <p style={{ color: "#ffff64" }}>
                    {song ? song?.title : null}
                  </p>
                </div>
              </>
            ))}
          </div>
        </div>
      ) : null}
    </>
  );
};
